var mixinMethod = {
  methods: {
    showErrorMessage (message) {
      this.$message({
        message: message,
        type: 'error'
      })
    },
    showSuccessMessage (message) {
      this.$message({
        message: message,
        type: 'success'
      })
    },
    showSuccessResult (res, message) {
      if (res.status == '2000') {
        this.$message({
          message: message,
          type: 'success'
        })
        return true
      }
      return false
    },
    showErrorMessageImport (res) {
      if (['DVS50001', 'DVS50002', 'DVS50003',
        'DVS5000A', 'DVS5000D', 'DVS5000G', 'DVS5000H',
        'DVS5000I', 'DVS5000K', 'DVS5000L', 'DVS5000M', 'DVS5000N', 'DVS5000Q', 'DVS5000R',
        'DVS5000S', 'DVS5000T', 'DVS5000W', 'DVS50005'].includes(res.status)) {
        this.showErrorMessage(res.message)
      } else {
        this.showErrorMessage('')
      }
    },
    showErrorMessageDefault (errCode) {
      var errorMessage = ''
      switch (errCode) {
        case 'DVS50001': errorMessage = ''; break
        case 'DVS50002': errorMessage = ''; break
        // case '': errorMessage =""
        default: ''
      }
      this.showErrorMessage(errorMessage)
    },
    showErrorMessageBox (res, params = {
      arr: [],
      colName: 'samplePkgAmtError'
    }) {
      var arr = params.arr
      var colName = params.colName
      var checked = true
      if (res.status === 'LIMSBOX101') {
        this.$message({
          message: '',
          type: 'error'
        })
        checked = false
      }
      if (res.status === 'LIMSBOX102') {
        this.$message({
          message: '' + res.message.replace('[', '').replace(']', ''),
          type: 'error'
        })
        checked = false
      }
      if (res.status === 'LIMSBOX103') {
        this.$message({
          message: '',
          type: 'error'
        })
        checked = false
      }
      if (res.status === 'LIMSSAM004') {
        this.$message({
          message: '',
          type: 'error'
        })
        checked = false
      }
      if (res.status === 'LIMSPAT002') {
        this.$message({
          message: '' + res.message.replace('[', '').replace(']', ''),
          type: 'error'
        })
        checked = false
      }
      if (res.status === 'LIMSLSR011') {
        this.$message({
          message: '' + res.message.replace('[', '').replace(']', ''),
          type: 'error'
        })
        checked = false
      }
      if (res.status === 'DVS5002') {
        this.$message({
          message: ',',
          type: 'error'
        })
        checked = false
      }
      if (!checked) {
        arr.map(item => {
          if (res.message.indexOf(item.sampleIdLims) > -1) {
            item[colName] = true
          }
        })
        this.updateTable()
      }
      return checked
    },
    showSrError (res) {
      if (res.status == 'Lims50002') {
        return true
      }
      return false
    },
    showSaveErrResult (res) {
      if (res.status == 'CE50002') {
        var str = '' + res.entity + ''
        this.$confirm(str)
        return
      }
      this.$message({
        type: 'error',
        message: ''
      })
    }
  }
}
export default mixinMethod
